.App {
  font-family: Arial, sans-serif;
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.6)),
      url(https://images.pexels.com/photos/1191710/pexels-photo-1191710.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1) center no-repeat;
  margin: 20px;
  text-align: center;
  height: 85vh;
 
}

h1 {
  color: #4CAF50;
  font-weight: bolder;
}

.loading {
  font-size: 20px;
  color: #FF9800;
}

.error {
  font-size: 20px;
  color: red;
}

.user-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.user-card {
  background: #f3d8af;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.user-card h2 {
  color: #333;
  font-size: 24px;
  margin-bottom: 10px;
}

.user-card p {
  color: #555;
  font-size: 16px;
}

.user-card a {
  color: #1E88E5;
  text-decoration: none;
}

.user-card a:hover {
  text-decoration: underline;
}
